@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-size: 18px;
  font-family: 'Montserrat', sans-serif;
}

.navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  margin: 0 auto;
  padding: 10px 0;
  z-index: 2;
  background: rgba(255, 255, 255, 0.9);
  border-bottom: 1px solid #999;
}

.navbar a {
  border: none;
  background: none;
  color: red;
  font-weight: bolder;
  cursor: pointer;
  font-variant: small-caps;
  font-size: 1.5rem;
  text-decoration: none;
  margin-right: 1vw;
}

.navbar a:hover {
  color: #eee;
}

.reg-btn {
  font-size: 1.4vw;
  color: #fff;
  padding: 5px 20px;
  font-variant: small-caps;
  border: none;
  cursor: pointer;
  border-radius: 10px;
  background: linear-gradient(90deg, rgb(53, 19, 19), rgb(139, 1, 1));
  animation: btn-animation 4s infinite;
}

.top-bar-btn {
  bottom: 7%;
  width: 200px;
  left: 0;
  right: 0;
  position: absolute;
  margin: 0 auto;
}

.reg-btn:hover {
  animation: btn-beat 1s infinite;
}

@keyframes btn-animation {
  5%,
  50% {
    transform: scale(1);
  }
  10% {
    transform: scale(0.95);
  }
  15% {
    transform: scale(1.05);
  }
  20% {
    transform: scale(1.05) rotate(-5deg);
  }
  25% {
    transform: scale(1.05) rotate(5deg);
  }
  30% {
    transform: scale(1.05) rotate(-3deg);
  }
  35% {
    transform: scale(1.05) rotate(2deg);
  }
  40% {
    transform: scale(1.05) rotate(0);
  }
}

@keyframes btn-beat {
  50% {
    transform: scale(1.1);
  }
}

.footer {
  height: 100px;
  background: linear-gradient(90deg, black, red);
  margin-top: -100px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  color: #eee;
  padding: 3rem;
}

.wrapper {
  background: fixed center no-repeat url('./images/test/bg.jpg');
}

.container {
  min-height: 100vh;
  padding-bottom: 100px;
  background-color: rgba(255, 255, 255, 0.4);
}

.top-bg {
  background: fixed no-repeat url('./images/test/top-bg.png');
  background-size: 100% auto;
}

.top-fg {
  min-height: calc(100vw * 0.5625);
  background: no-repeat url('./images/test/top-fg.png');
  background-size: 100% auto;
}

.sim-screen {
  height: 800px;
  background: center no-repeat url('./images/test/mid-screen.jpg');
  clip-path: polygon(100% 0, 100% 90%, 50% 100%, 0 90%, 0 0);
}

.sim-info {
  height: 90%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  font-size: 2rem;
}

.sim-info-top {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.rules-and-prizes {
  margin: 4rem;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.rules {
  counter-reset: li;
  list-style: none;
  *list-style: decimal;
  padding: 0;
  /* text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5); */
}

.rules span {
  position: relative;
  display: block;
  padding: 0.4em 0.4em 0.4em 2em;
  *padding: 0.4em;
  margin: 0.8em 0;
  background: rgba(255, 255, 255, 0.8);
  color: #333;
  text-decoration: none;
  border-radius: 0.3em;
  transition: all 0.3s ease-out;
}

.rules span:hover {
  background: #eee;
}

.rules span:hover::before {
  transform: rotate(360deg);
}

.rules span::before {
  content: counter(li);
  counter-increment: li;
  position: absolute;
  left: -1.3em;
  top: 50%;
  margin-top: -1.3em;
  background: #612121;
  height: 2em;
  width: 2em;
  color: #fff;
  line-height: 2em;
  border: 0.3em solid #fff;
  text-align: center;
  font-weight: bold;
  border-radius: 2em;
  transition: all 0.3s ease-out;
}

.prizes {
  padding: 2rem;
  min-width: 375px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: rgba(0, 0, 0, 0.8);
  color: #fff;
  border-radius: 20px;
  margin-left: 2rem;
  text-align: center;
  border: 4px solid #fff;
}

.prizes h2 {
  font-size: 1.4rem;
}

.prizes h3 {
  width: 100%;
  margin-top: 1rem;
  padding-top: 0.5rem;
  margin-bottom: 0.5rem;
  border-top: 2px solid #fff;
}

.flex-line {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.classes {
  height: 1000px;
  background: center no-repeat url('./images/test/class_t.jpg');
  clip-path: polygon(100% 20%, 100% 90%, 50% 100%, 0 90%, 0 0);
}

.qual {
  height: 100%;
  background: no-repeat url('./images/test/qual.png');
  background-position-x: center;
  background-position-y: 5rem;

  color: #fff;
  font-variant: small-caps;
  font-size: 2rem;

  display: flex;
  flex-direction: column;
  justify-content: end;
  align-items: center;
}

.qual span {
  text-align: center;
  width: 460px;
  margin-bottom: 6rem;
}

.partials {
  z-index: -1;
  height: 800px;
  margin-top: -200px;
  background-color: rgba(255, 255, 255, 0.4);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.bottom-left {
  height: 100%;
  width: 33%;
  background: no-repeat url('./images/test/bottom-l.jpg');
}

.bottom-mid {
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  
}

.bottom-right {
  height: 100%;
  width: 40%;
  background: no-repeat url('./images/test/bottom-r.png');
  background-position-y: 150px;
  background-position-x: 100px;
  background-size: 100%;
}

.box {
  position: relative;
  min-width: 300px;
  max-width: 700px;
  padding: 20px;
  box-shadow: 0 0 20px rgb(139, 0, 0);
  background-color: rgba(0,0,0,1);
  color: #fff;
  border-radius: 4px;
  margin-bottom: 30px;
  font-size: 1.3rem;
}

.box:before {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  width: 30px;
  height: 30px;
  background-color: rgba(0,0,0,1);
  box-shadow: 0 0 20px rgb(139, 0, 0);
  transform: translate(50%, -50%) rotate(45deg);
  -webkit-clip-path: polygon(-20px -20px, calc(100% + 20px) -20px, calc(100% + 20px) calc(100% + 20px));
          clip-path: polygon(-20px -20px, calc(100% + 20px) -20px, calc(100% + 20px) calc(100% + 20px));
}
.box--left:before {
  right: auto;
  left: 0;
  transform: translate(-50%, -50%) rotate(-135deg);
}

.box span {
  font-size: 1.4rem;
  font-weight: bolder;
}

@media (max-width: 1200px) {
  .rules-and-prizes {
    flex-direction: column;
  }

  .prizes {
    margin-left: 0;
    margin-top: 2rem;
  }
}

@media (max-width: 800px) {
  .top-bg {
    background-position-y: 1rem;
    margin-top: 1rem;
  }

  .sim-screen {
    height: 600px;
    background-size: 200%;
  }

  .sim-info {
    font-size: 1.5rem;
  }

  .sim-info-top {
    margin-top: 1rem;
  }

  .qual {
    background-size: 100%;
  }
}

@media (max-width: 600px) {
  .top-bg {
    background-position-y: 2rem;
    margin-top: 2rem;
  }

  .sim-screen {
    height: 400px;
    background-size: 200%;
  }

  .sim-info {
    height: 85%;
    font-size: 1rem;
  }
}
